<!--血压入口-->
<template>
    <!--头部-->
    <s-header :name="'血压'" :rightName="'历史数据'" :rightPress="rightclick"/>
    <!--选择设备-->
    <change-device fromPage="bloodpressure" :backgroundColor="'#06B48D'"  :otherAddClick="onAddRecord"/>
    <div class="div_content">
        <div class="div_top">
            <span>最近一次测量</span>
            <span>{{ detial.measureTimeDesc }}</span>
            <!--圆环显示-->
          <div :style="{ width:'100%',marginLeft: '10px', marginRight: '10px',paddingLeft: '20px'}" >
              <three-circle
                    :dataleft="detial.highPressure"
                    :datacenter="detial.lowPressure"
                    :dataright="detial.heartRate"
                    :sizecenter="'85px'"
                    :fontsizeleft="'20px'"
                    :fontsizecenter="'20px'"
                    :fontsizeright="'20px'"
                    :height="'100px !important'"
              />
            </div>
            <div class="div_des">
                <span>高压mmHg</span>
                <span>高压mmHg</span>
                <span>脉搏 次/分</span>
            </div>

            <!--血压状态-->
            <track-type :datas="['低血压','正常','临界值','轻度','中度','重度']" :type="detial.type + 1"/>

            <div v-for="(item,index) in bloodpressuredata" :key="index" style="padding: 10px; width: 100%">
              <van-divider>{{item.title}}</van-divider>
                <div style="display: flex;justify-content: space-between; padding-left: 18px; padding-right: 18px">
                    <div class="div_column">
                        <span class="span_data">{{item.max||'--'}}</span>
                        <span>峰值</span>
                    </div>
                    <div class="div_column">
                        <span class="span_data">{{item.min||'--'}}</span>
                        <span>谷值</span>
                    </div>
                    <div class="div_column">
                        <span class="span_data">{{item.average||'--'}}</span>
                        <span>均值</span>
                    </div>
                </div>
            </div>
        </div>
      <track-data :title="'温馨提示'">
        <template #inner >
          {{ detial.typeStr }}
        </template>
      </track-data>

    </div>
</template>

<script>
  import sHeader from '@/components/SimpleHeader'
  import ThreeCircle from '../../../components/ThreeCircle'
  import {reactive, onMounted, toRefs} from 'vue'
  import ChangeDevice from '../components/ChangeDevice'
  import TrackType from '../components/TrackType'
  import TrackData from '../TrackData'
  import {useRouter} from "vue-router";
  import moment from 'moment'
  import {getSyLogBloodPressureHistoryDetailByUserId} from "@/service/bloodpressure";
  import {formatYYMMDD} from "@/utils/util";

  export default {
    name: "index",
    components: {
      sHeader,
      ChangeDevice,
      ThreeCircle,
      TrackType,
      TrackData,
    },
    setup() {
      const router = useRouter()

      onMounted(
        () => {
          requestDayDetial()
        }
      )

      // 通过reactive 可以初始化一个可响应的数据，与Vue2.0中的Vue.observer很相似
      const state = reactive({
        todo: '',
        selectdate: new Date(),
        bloodpressuredata: [{
          title:'高压',
          max:'',
          min:'',
          average:''
        },
          {title:'低压',
            max:'',
            min:'',
            average:''},
          {title:'脉搏',
            max:'',
            min:'',
            average:''}],
        detial:{
          measureTimeDesc: "---",
          highPressure: 0, // 低压
          lowPressure: 0, // 高压
          heartRate: 0, // 脉搏
          type: 0,
          typeStr: "暂无数据",
          avgHeartRate: 0,
          avgHighPressure: 0,
          avgLowPressure: 0,
          maxHeartRate:0,
          maxHighPressure:222,
          maxLowPressure:200,
          minHeartRate:0,
          minHighPressure:222,
          minLowPressure:200,
        },
      })

      // 获取详情
      const requestDayDetial = async () => {
        console.log( formatYYMMDD(state.selectdate),state.selectdate)
        const res = await getSyLogBloodPressureHistoryDetailByUserId( formatYYMMDD(state.selectdate), formatYYMMDD(state.selectdate))
        const {code,data} = res
        if(code !== 200) return

        state.detial = code === 200  ?data : {}
        state.bloodpressuredata[0].average =  state.detial.avgHighPressure
        state.bloodpressuredata[0].min =  state.detial.minHighPressure
        state.bloodpressuredata[0].max =  state.detial.maxHighPressure
        state.bloodpressuredata[1].average =  state.detial.avgLowPressure
        state.bloodpressuredata[1].min =  state.detial.minLowPressure
        state.bloodpressuredata[1].max =  state.detial.maxLowPressure
        state.bloodpressuredata[2].average =  state.detial.avgHeartRate
        state.bloodpressuredata[2].min =  state.detial.minHeartRate
        state.bloodpressuredata[2].max =  state.detial.maxHeartRate
      }


      const onAddRecord = () =>{
        router.push("/bloodpressure-add-recode")
      }
      const rightclick = () => {
        router.push("/history-blood-record")

      }
      const onTypeClick = (index) => {
        alert(index)
      }
      const onchange = (data) => {
        state.selectdate = data
      }
      // 在Vue3.0中，所有的数据和方法都通过在setup 中 return 出去，然后在template中使用
      return {
        ...toRefs(state),
        onTypeClick,
        onchange,
        onAddRecord,
        rightclick
      }
    }
  }
</script>

