        padding: 10px 0px 10px 0px;
        align-items: center;
        display: flex;
        flex-direction: column;
    }

    .div_line {
        background-color: #f6f6f6;
        height: 1px;
        width: 136px;
    }

    .span_data {
        color: #06B48D;
        font-size: 12px;
        margin: 10px 0px;
